import React from 'react';
import Link from 'next/link';
import { useRouter } from 'next/router';

const Navbar: React.FC = () => {
  const router = useRouter();
  return (
    <nav className="bg-gray-900 py-4">
      <div className="max-w-7xl mx-auto px-4">
        <div className="flex justify-center space-x-2 rtl:space-x-reverse">
          {[
            { href: '/', label: 'الرئيسية' },
            { href: '/services', label: 'خدماتنا' },
            { href: '/projects', label: 'أعمالنا' },
            { href: '/blogs', label: 'مدونات' },
            { href: '/about', label: 'من نحن' },
            { href: '/policy', label: 'سياستنا' },
            { href: '/contact', label: 'للتواصل' },
          ].map((item) => (
            <Link key={item.href} href={item.href}>
              <span
                className={`relative block px-4 py-2 font-bold text-black bg-white rounded-full transition-all duration-300 ${
                  router.pathname === item.href
                    ? 'bg-blue-600 text-white before:absolute before:inset-0 before:bg-blue-600 before:rounded-full'
                    : 'hover:bg-blue-500 hover:text-white'
                }`}
              >
                {item.label}
              </span>
            </Link>
          ))}
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
